body {
  margin: 0;
  background-color: #570080;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  /* padding: 5px; */
}
#root {
  max-width: 50%;
  width: 100%;
}
.App {
  background-color: #350d48;
  height: 500px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.App h1 {
  color: #fff;
}

.add-task {
  width: 100%;
  display: flex;
  justify-content: center;
}
.add-task input {
  width: 70%;
  border: solid 1px #570080;
  background-color: #350d48;
  height: 40px;
  padding-left: 5px;
  caret-color: #fff;
  color: #fff;
  font-size: 20px;
}

.add-task input:focus {
  outline: none;
}
.add-task-btn {
  background-color: #570080;
  color: #fff;
  border: none;
}
ul {
  margin: 0;
  padding: 20px 0 0 0;
  width: 85%;
  display: flex;
  flex-direction: column;
  gap: 10px;
  list-style: none;
  overflow: auto;
}
.to-do-list {
  background-color: #570080;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-radius: 5px;
  padding: 0 10px;
  margin-bottom: 10px;
  color: #fff;
}

.font-icon {
  color: #fff;
  font-size: 20px;
  margin-right: 10px;
}

@media screen and (max-width: 720px) {
  #root {
    max-width: 70%;
  }
}

@media screen and (max-width: 500px) {
  #root {
    max-width: 90%;
  }
}
